// @flow
import React, { useState, useEffect } from 'react';
import { CSSTransition } from 'react-transition-group';
import './support.css';

const Support = () => {
  const [transition, setTransition] = useState(false);

  useEffect(() => {
    setTransition(true);

    return () => setTransition(false);
  }, []);

  return (
    <CSSTransition in={transition} timeout={1000} classNames="fade">
      <div className="support">
        <h5>Support</h5>
        <h6>
          <a
            href="https://patreon.com/vincortez"
            rel="noopener noreferrer"
            target="_blank"
            className="waves-effect waves-light"
          >
            Patreon
          </a>
        </h6>
        <h6>
          <a
            href="https://subscribestar.com/vincortez"
            rel="noopener noreferrer"
            target="_blank"
            className="waves-effect waves-light"
          >
            SubscribeStar
          </a>
        </h6>
      </div>
    </CSSTransition>
  );
};

export default Support;
